___CSS_LOADER_EXPORT___.push([module.id, `/* General Styles */
body {
  font-family: Arial, sans-serif;
  text-align: center;
}

h1 {
  margin-top: 0;
}

.main {
  display: flex;
  justify-content: space-around;
  gap: 10px;
}

.board {
  margin: 10px auto;
}

.row {
  display: flex;
}

.cell {
  width: 30px;
  height: 30px;
  border: 1px solid black;
  position: relative; /* Added to position hit/miss circles */
}

.cell:hover {
  background-color: #f0f0f0;
  cursor: pointer;
}

.ship {
  background-color: blue;
}

.ship.vertical::after {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
}

.ship.horizontal::after {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
}

.hit,
.miss {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 10px);
  left: calc(50% - 10px);
}

.hit {
  background-color: green;
}

.miss {
  background-color: red;
}

#ship-selection {
  margin-top: 20px;
}

#ship-list {
  list-style: none;
  padding: 0;
}

#ship-list li {
  padding: 5px;
  cursor: pointer;
}

#ship-list li.selected {
  background-color: yellow;
}

.game-info {
  margin-top: 10px;
}

dialog::backdrop {
  background: rgba(0, 0, 0, 0.5);
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
}
